<template>
    <div>
        <MainPageHeader/>
        <PageContent class="content">
            <AccountSearch :value="query" @input="handleSearchInput" @search="handleSearch" />
            <div class="account-list" :class="accountListClass">
                <AccountRow v-for="account in accounts" :key="account.key" :account="account" />
            </div>
        </PageContent>
        <ContentLoader v-if="!contentLoaded" />
    </div>
</template>

<script>
    import MainPageHeader from '../components/MainPageHeader.vue';
    import PageContent from '../components/generic/PageContent.vue';
    import AccountRow from '../components/accountList/AccountRow.vue';
    import AccountSearch from '../components/accountList/AccountSearch.vue';
    import ContentLoader from '../components/ContentLoader.vue';
    import { createCancellable } from '../helpers/asyncHelper';
    import { getAccountService } from '../services/accountService';
    import { limitView } from '../helpers/permissionHelper';
    import { ADMIN } from '../types/role.types';

    export default {
        name: 'Tab1',
        components: {
            MainPageHeader,
            PageContent,
            AccountRow,
            AccountSearch,
            ContentLoader,
        },
        data() {
            return {
                accounts: [],
                contentLoaded: false,
                query: '',
                searching: false,
            };
        },
        computed: {
            accountListClass() {
                return {
                    searching: this.searching,
                };
            },
        },
        methods: {
            handleSearchInput(query) {
                this.query = query;
            },

            handleSearch(query) {
                this.searching = true;

                if (this.accountFetchAction) {
                    this.accountFetchAction.cancel();
                }

                this.accountFetchAction = createCancellable(() => getAccountService().getAccounts(query));

                this.accountFetchAction().then((accounts) => {
                    this.accounts = accounts;
                    this.searching = false;
                    this.accountFetchAction = null;
                });
            },
        },
        async mounted() {
            if (await limitView(this.$router, [ADMIN])) {
                return;
            }

            this.accounts = await getAccountService().getAccounts(this.query);
            this.contentLoaded = true;
        },
    };
</script>

<style lang="scss" scoped>
    .content {
        max-width: 600px;

        .header {
            h1 {
                color: #666;
                font-size: 1.5em;
            }

            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .account-list {
            transition: 0.1s ease-out opacity;

            &.searching {
                opacity: 0.5;
            }
        }
    }
</style>
