<template>
    <p class="account-role" v-if="this.account.parentUserKey === undefined" :class="roleClass">{{ roleTitle }}</p>
</template>

<script>
    export default {
        name: 'RoleTag',
        props: {
            account: {
                required: true,
            },
        },
        computed: {
            roleTitle() {
                return this.account.parentUserKey !== undefined ? 'Sub-User' : 'User';
            },

            roleClass() {
                return this.account.parentUserKey !== undefined ? 'sub-user' : 'user';
            },
        },
    };
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';

    .account-role {
        pointer-events: none;
        user-select: none;
        width: 50px;
        padding: 6px;
        margin-right: 10px;
        margin-left: -60px;
        font-size: 0.8em;
        text-align: center;

        background-color: $black;
        border: solid 2px $primary;
        border-radius: 4px;
        color: $white;

        &.border-white {
            border-color: $white;
        }

        &.border-primary {
            border-color: $primary;
        }

        &.sub-user {
            background-color: rgb(0, 255, 200);
        }
    }
</style>
