<template>
    <div class="AccountRow">
        <RoleTag :account="account" />
        <p class="account-name">{{ account.email }}</p>
        <div class="actions">
            <SmallButton icon="sign-in-alt" border-color="white" @click="logOver">
                Przeloguj
            </SmallButton>
        </div>
    </div>
</template>

<script>
    import { library } from '@fortawesome/fontawesome-svg-core';
    import { faSignInAlt } from '@fortawesome/free-solid-svg-icons';
    import SmallButton from '@/components/input/SmallButton.vue';
    import RoleTag from '../RoleTag.vue';
    import { getAuthService } from '../../services/authService';

    library.add(faSignInAlt);

    export default {
        name: 'AccountRow',
        components: { SmallButton, RoleTag },
        props: {
            account: {
                required: true,
            },
        },
        computed: {
            roleTitle() {
                return this.account.parentUserKey !== undefined ? 'Sub-User' : 'User';
            },

            roleClass() {
                return this.account.parentUserKey !== undefined ? 'sub-user' : 'user';
            },
        },
        methods: {
            async logOver() {
                const result = await getAuthService().manageUser(this.account.key);

                if (result) {
                    this.$router.go();
                }
            },
        },
    };
</script>

